import React from 'react';
import './style.css';

//          interface:      Props          //
interface ForceExitModalProps {
    onForceExit: () => void;
    onClose: () => void;
    userName: string;
}

//          component:               //
export default function ForceExitModal ({ onForceExit, onClose, userName }: ForceExitModalProps) {

    //          render:                //
    return (
        <div className='force-exit-modal-card'>
            <div className='force-exit-modal-box'>
                <div className='force-exit-modal-title'>{`'${userName}'  ?`}</div>
                <div className='force-exit-modal-description-box'>
                    <div className='force-exit-modal-notify-box'>
                        <div className='warning-icon'></div>
                        <div className='force-exit-modal-notify-text'>{'      .'}</div>
                    </div>
                    <div className='force-exit-select'>
                        <div className='force-exit-button' onClick={onForceExit}>{'  '}</div>
                        <div className='force-exit-cancel' onClick={onClose}>{''}</div>
                    </div>
                </div>
            </div>
        </div>
    );
}