import React, { useRef, useState } from 'react'
import './style.css';
import { useLocation, useNavigate } from 'react-router-dom';

interface HeaderProps {
  onSearchMoveClickHandler: () => void
}

export default function Header({ onSearchMoveClickHandler }: HeaderProps) {

  //        state: path name        //
  const { pathname } = useLocation();

  //        function:         //
  const navigator = useNavigate();

  //        event handler:            //
  const onLogoClickHandler = () => {
    navigator('/main');
  }
  
  //        event handler:            //
  const onStudyCreateNavigatorHandler = () => {
    navigator('/studycreate');
  }
  //        event handler:            //
  const onMypageNavigatorHandler = () => {
    navigator('/mypage/:email');
  }
    
  //        render:           //
  return (
    <div id='header'>
      <div className='header-container' >
        <div className='header-left-box'>
          <div className='header-logo-icon-box'>
            <div className='logo-icon'></div>
          </div>
          <div className='header-logo-text' onClick={onLogoClickHandler}>{''}</div>
        </div>

        <div className='header-right-box'>
          <div className='header-study-search-button' onClick={onSearchMoveClickHandler}>{' '}</div>
          <div className='header-study-create-button' onClick={onStudyCreateNavigatorHandler}>{' '}</div>
          <div className='header-study-mypage-button' onClick={onMypageNavigatorHandler}>{'  '}</div>
        </div>
      </div>
    </div>
  )
}
