import React from 'react';
import { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { ko } from "date-fns/esm/locale";
import './style.css';

//          interface:  interface Props          //
interface Props {
    startDate: Date | null;
    endDate: Date | null;
    onStartDateChange: (date: Date) => void;
    onEndDateChange: (date: Date) => void;
}

//          component:            //
const DatePickerComponent = ({ startDate, endDate, onStartDateChange, onEndDateChange }: Props) => {
          
    // //          state:             //
    // const [startDate, setstartDate] = useState<Date | null>(null);
    // //          state:             //
    // const [endDate, setEndDate] = useState<Date | null>(null);

    //          event handler:             //
    const startDateHandle = (date: Date) => {
        console.log(date);
        onStartDateChange(date);
    };
    //          event handler:             //
    const endDateHandle = (date: Date) => {
        onEndDateChange(date);
    };

    //          render:             //
    return (
        <div className='datepicker-box'>
            <div className='start-date-box'>
                <DatePicker className='start-date' showPopperArrow={false} locale={ko} 
                selected={startDate} onChange={startDateHandle} closeOnScroll={true} minDate={new Date()} dateFormat='yyyy MM dd' dateFormatCalendar='yyyy MM'
                placeholderText='  .' />
            </div>
            <div className='end-date-box'>
                <DatePicker className='end-date' showPopperArrow={false} locale={ko} 
                selected={endDate} onChange={endDateHandle} closeOnScroll={true} minDate={startDate} dateFormat='yyyy MM dd' dateFormatCalendar='yyyy MM' 
                placeholderText='  .' />
            </div>
        </div>
    );
};

export default DatePickerComponent;