import './style.css';

import { useState, useRef, useEffect, ChangeEvent } from 'react';
import ModalSideMenu from 'components/ModalSideMenu';
import Scrollbars from 'react-custom-scrollbars-2';

export default function StudyDateModal({ modalCloseHandler, studyNumber }: {modalCloseHandler: () => void; studyNumber: string | number}) {
    //          state :                  //
    const [timeError, setTimeError] = useState<boolean>(false);
    //          state:            //
    const [isOpen, setIsOpen] = useState(false);
    //          state :               //
    const [isdateSelect, setIsDateSelect] = useState<boolean>(false);

    let InputError = false;

    //          event handler:               //
    const onDateSelectClickHandler = () => {
        setIsOpen(!isOpen);
    };

    //          component:              //
    const StudyAlertCard = () => {
        //          state :              //
        const [dateError, setDateError] = useState<boolean>(false);

        return (
            <div className='study-date-contents-box'>
                <div className='study-date-contents-icon'></div>
                {<div className='study-date-contents-message'>{'    !'}</div>}
            </div>
        );
    };

    //          component :  (hour)              //
    const StartHourDropDownBox = () => {
        //          state:            //
        const [isOpen, setIsOpen] = useState(false);
        //          state:             //
        const [selectedItem, setSelectedItem] = useState<string | null>(null);
        //          state:   ref           //
        const dropdownRef = useRef<HTMLDivElement | null>(null);

        //          event handler:                 //
        const toggleDropdownClickHandler = () => {
            setIsOpen(!isOpen);
        };

        //          function:               //
        const selectItem = (item: string) => {
            setSelectedItem(item);
            setIsOpen(false);
        };

        //          effect:                  //
        useEffect(() => {
            function handleClickOutside(event: MouseEvent) {
                if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
                    setIsOpen(false);
                }
            }

            document.addEventListener('click', handleClickOutside);
            return () => {
                document.removeEventListener('click', handleClickOutside);
            };
        }, []);

        // description:   (hour)    //
        const items = [];
        for (let i = 0; i <= 11; i++) {
            items.push(`${i}`);
        }

        //          event handler:                //
        const startHourChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
            if (!event.target.value) {
                setTimeError(true);
                return;
            }
            const startHour = event.target.value;
            setSelectedItem(startHour);
        };

        if (selectedItem === null) {
            setTimeError(true);
        }

        //  event handler:            //
        // useEffect(()=>{
        //     if(selectedItem === null){
        //         setTimeError(true);
        //         return;
        //     }
        //     setSelectedItem(selectedItem);
        //     setTimeError(false);
        //     return;
        // },[selectedItem]);

        //          render: DropDown   (hour)            //
        return (
            <div ref={dropdownRef} className='dropdown-study-time-set-box' onClick={toggleDropdownClickHandler}>
                <div className='study-hour-select' onChange={startHourChangeHandler}>
                    {selectedItem}
                </div>
                <div className='study-date-down-icon'></div>
                {isOpen && (
                    <div className='dropdown-study-time-set-list'>
                        <Scrollbars renderTrackVertical={(props) => <div {...props} className='track-time-set-vertical' />} renderThumbVertical={(props) => <div {...props} className='thumb-time-set-vertical' />}>
                            {items.map((item) => (
                                <div className='dropdown-study-time-set-list-index' key={item} onClick={() => selectItem(item)}>
                                    {item}
                                </div>
                            ))}
                        </Scrollbars>
                    </div>
                )}
            </div>
        );
    };

    //          component :  (minute)              //
    const StartMinutesDropDownBox = () => {
        //          state:            //
        const [isOpen, setIsOpen] = useState(false);
        //          state:             //
        const [selectedItem, setSelectedItem] = useState<string | null>(null);
        //          state:   ref           //
        const dropdownRef = useRef<HTMLDivElement | null>(null);

        if (selectedItem === null) {
            setTimeError(true);
        }

        //          event handler:                 //
        const toggleDropdownClickHandler = () => {
            setIsOpen(!isOpen);
        };

        //          function:               //
        const selectItem = (item: string) => {
            setSelectedItem(item);
            setIsOpen(false);
        };

        if (selectedItem === null) {
            setTimeError(true);
        }

        //          effect:                  //
        useEffect(() => {
            function handleClickOutside(event: MouseEvent) {
                if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
                    setIsOpen(false);
                }
            }

            document.addEventListener('click', handleClickOutside);
            return () => {
                document.removeEventListener('click', handleClickOutside);
            };
        }, []);

        // description:     //
        const items = [];
        for (let i = 0; i <= 59; i += 5) {
            //     ,   0 
            const formattedNumber = i < 10 ? `0${i}` : `${i}`;
            items.push(formattedNumber);
        }

        if (selectedItem === null) {
            setTimeError(true);
        }

        //          event handler:   (miniutes)              //
        const startMinuteChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
            if (!event.target.value) {
                setTimeError(true);
                return;
            }
            const startHour = event.target.value;
            setSelectedItem(startHour);
        };

        //          render: DropDown   (minutes)            //
        return (
            <div ref={dropdownRef} className='dropdown-study-time-set-box' onClick={toggleDropdownClickHandler}>
                <div className='study-hour-select' onChange={startMinuteChangeHandler}>
                    {selectedItem}
                </div>
                <div className='study-date-down-icon'></div>
                {isOpen && (
                    <div className='dropdown-study-time-set-list'>
                        <Scrollbars renderTrackVertical={(props) => <div {...props} className='track-time-set-vertical' />} renderThumbVertical={(props) => <div {...props} className='thumb-time-set-vertical' />}>
                            {items.map((item) => (
                                <div className='dropdown-study-time-set-list-index' key={item} onClick={() => selectItem(item)}>
                                    {item}
                                </div>
                            ))}
                        </Scrollbars>
                    </div>
                )}
            </div>
        );
    };

    //          component :                //
    const StartTimeComboBox = () => {
        //          state:               //
        const [isClicked, setIsClicked] = useState(false);
        //          state:              //
        const [startHour, setStartHour] = useState<number>();
        //          state:               //
        const [startHourSelectError, setStartHourSelectError] = useState<boolean>(false);

        //          event handler:              //
        const handleClickHandler = () => {
            if (startHour === null) return;
            setIsClicked(!isClicked);
        };

        // render : //
        return (
            <div className='study-box-wrapper'>
                <StartHourDropDownBox />
                <div className='division'>{':'}</div>
                <StartMinutesDropDownBox />
            </div>
        );
    };

    //          component:  (hour)              //
    const EndHourDropDownBox = () => {
        //          state:            //
        const [isOpen, setIsOpen] = useState(false);
        //          state:             //
        const [selectedItem, setSelectedItem] = useState<string | null>(null);
        //          state:   ref           //
        const dropdownRef = useRef<HTMLDivElement | null>(null);

        if (selectedItem === null) {
            setTimeError(true);
        }

        //          event handler:                 //
        const toggleDropdownClickHandler = () => {
            setIsOpen(!isOpen);
        };

        //          function:               //
        const selectItem = (item: string) => {
            setSelectedItem(item);
            setIsOpen(false);
        };

        //          effect:                  //
        useEffect(() => {
            function handleClickOutside(event: MouseEvent) {
                if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
                    setIsOpen(false);
                }
            }

            document.addEventListener('click', handleClickOutside);
            return () => {
                document.removeEventListener('click', handleClickOutside);
            };
        }, []);

        // description: (hour)    //
        const items = [];
        for (let i = 0; i <= 11; i++) {
            items.push(`${i}`);
        }

        //          event handler:  (hour)              //
        const startHourChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
            if (!event.target.value) {
                setTimeError(true);
                return;
            }
            const startHour = event.target.value;
            setSelectedItem(startHour);
        };

        //          render: DropDown   (hour)            //
        return (
            <div ref={dropdownRef} className='dropdown-study-time-set-box' onClick={toggleDropdownClickHandler}>
                <div className='study-hour-select' onChange={startHourChangeHandler}>
                    {selectedItem}
                </div>
                <div className='study-date-down-icon'></div>
                {isOpen && (
                    <div className='dropdown-study-time-set-list'>
                        <Scrollbars renderTrackVertical={(props) => <div {...props} className='track-time-set-vertical' />} renderThumbVertical={(props) => <div {...props} className='thumb-time-set-vertical' />}>
                            {items.map((item) => (
                                <div className='dropdown-study-time-set-list-index' key={item} onClick={() => selectItem(item)}>
                                    {item}
                                </div>
                            ))}
                        </Scrollbars>
                    </div>
                )}
            </div>
        );
    };

    //          component :  (minutes)              //
    const EndMinutesDropDownBox = () => {
        //          state:            //
        const [isOpen, setIsOpen] = useState(false);
        //          state:             //
        const [selectedItem, setSelectedItem] = useState<string | null>(null);
        //          state:   ref           //
        const dropdownRef = useRef<HTMLDivElement | null>(null);

        if (selectedItem === null) {
            setTimeError(true);
        }

        //          event handler:                 //
        const toggleDropdownClickHandler = () => {
            setIsOpen(!isOpen);
        };

        //          function:               //
        const selectItem = (item: string) => {
            setSelectedItem(item);
            setIsOpen(false);
        };

        //          effect:                  //
        useEffect(() => {
            function handleClickOutside(event: MouseEvent) {
                if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
                    setIsOpen(false);
                }
            }

            document.addEventListener('click', handleClickOutside);
            return () => {
                document.removeEventListener('click', handleClickOutside);
            };
        }, []);

        // description:      //
        const items = [];
        for (let i = 0; i <= 59; i += 5) {
            //     ,   0 
            const formattedNumber = i < 10 ? `0${i}` : `${i}`;
            items.push(formattedNumber);
        }

        //          event handler:   (miniutes)              //
        const endMinuteChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
            if (!event.target.value) {
                setTimeError(true);
                return;
            }
            const startHour = event.target.value;
            setSelectedItem(startHour);
        };

        //          render: DropDown   (minutes)            //
        return (
            <div ref={dropdownRef} className='dropdown-study-time-set-box' onClick={toggleDropdownClickHandler}>
                <div className='study-hour-select' onChange={endMinuteChangeHandler}>
                    {selectedItem}
                </div>
                <div className='study-date-down-icon'></div>
                {isOpen && (
                    <div className='dropdown-study-time-set-list'>
                        <Scrollbars 
                        renderTrackVertical={(props) => <div {...props} className='track-time-set-vertical' />} 
                        renderThumbVertical={(props) => <div {...props} className='thumb-time-set-vertical' />}>
                            {items.map((item) => (
                                <div className='dropdown-study-time-set-list-index' key={item} onClick={() => selectItem(item)}>
                                    {item}
                                </div>
                            ))}
                        </Scrollbars>
                    </div>
                )}
            </div>
        );
    };

    //          component :                //
    const EndTimeComboBox = () => {
        const [isClicked, setIsClicked] = useState(false);

        const handleClickHandler = () => {
            setIsClicked(!isClicked);
        };

        //          state :              //
        const [endHour, setEneHour] = useState<number | null>();
        //          state :              //
        const [endMinute, setEndMinute] = useState<number | null>();

        // render : //
        return (
            <div className='study-box-wrapper'>
                <EndHourDropDownBox />
                <div className='division'>{':'}</div>
                <EndMinutesDropDownBox />
            </div>
        );
    };

    //          component :             //
    const CalendarCard = () => {
        // render : //
        return (
            <div className='study-date-set-box'>
                <div className='study-date-schedule'>{`  : 00 00 00`}</div>
                <div className='study-date-hour-box'>
                    <div className='study-date-hour-title'>{' : '}</div>
                    <StartTimeComboBox />
                    <div className='between'>{'~'}</div>
                    <EndTimeComboBox />
                </div>
            </div>
        );
    };

    //          event handler:                 //
    const onInputErrorCheckChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        if (timeError) {
            InputError = timeError;
            return;
        }
    };

    // render :     //
    return (
        <div id='notice-wrapper'>
            <div className='reset-card'>
                <div className='notice-button-box'>
                    <button type='button' className='modal-close-button' onClick={modalCloseHandler}>
                        X
                    </button>
                </div>
                <div className='room-box'>
                    <div className='room-name'>{'  '}</div>
                    <div className='room-schedule'>{' '}</div>
                    <div className='room-date-set-box'>
                        <div className='room-date-control'></div>
                        <div className='room-date-contents'>
                            <div className='next-study-date'>{' '}</div>
                            <div className='study-date-modal-box'>
                                {
                                    <CalendarCard />
                                    // <StudyAlertCard/>
                                }
                            </div>
                            {InputError && <div className='study-hour-error'>{'   .'}</div>}
                        </div>
                    </div>
                    <div className='insert-box' onChange={onInputErrorCheckChangeHandler}>{''}</div>
                </div>
            </div>
        </div>
    );
}
